@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

body{
  font-family: 'Roboto', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/***********Navbar*******/
nav a.nav-link {
  font-weight: 300;
  letter-spacing: 0.0375rem;
}
/**************Uppercase for NAvbar****/
nav ul li {
  text-transform:uppercase;
}
/************Link Color********/
.navbar-nav > li > a {
  color: ghostwhite !important;
  margin-left: 0.75rem;
  margin-left: 0.75rem;
  border-top: 00.1875rem solid rgb(17, 17, 17);
}
/********Resume Color**********/
.navbar-brand {
  color: crimson !important;
  text-transform: uppercase;
  text-shadow: darkgoldenrod !important;
  font-weight: bold;
  max-width: 16.25rem;
  padding: 0 !important;
}
@media(max-width:768px){
  .navbar-brand{
    width: 7rem;
  }
}
/*******for Navbar border link color********/
nav{
  z-index: 2;
}
.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid orange;
}
/******** for hovering *********/
.navbar-nav > li > a:hover{
  border-top: 0.1875rem solid orange;
}
/*******Header************/
.header-wrapper{
  position: relative;
  background: url(./Images/wall-and-laptop-background.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height:100vh;
}
.main-info{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  position:absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.main-info h1{
  color:orange !important;
  text-transform: uppercase;
}
/*..............Typed...........*/
.typed-text{
  color: white !important;
  font-size: 2rem;
  text-align: center;
  align-items: center;
  flex-direction:center;
  display: flex;
  justify-content: center;
}
@media(max-width:768px){
  .main-info h1{
   font-size: 1.7rem;
  }
  .typed-text{
   font-size: 1.3rem;
  }
}
/*.................button.............*/
.btn-main-offer{
  background-color: orange;
  border:1px solid orange;
  text-transform: uppercase;
  align-items: center;
  justify-content: center;
  display: flex;
  text-align: center;
  margin:2rem;
  border-radius: 0;
  padding: 0.675rem 0.75rem 0.75rem 0.75rem;
  z-index: 2;
}
.btn-main-offer:hover{
  text-decoration: none;
  background-color: orangered !important;
}

.tsparticles-canvas-el{
  height: 100vh;
  width:100vw;
  position: absolute;
  z-index: 1;
  
}

@media(max-width:768px){
  .tsparticles-canvas-el{
    font-size: 1.7px;
  }

}
/*********About me********/
.photo-wrap{
  display:flex;
  justify-content: center;
  align-items: center;
}
.profile-img{
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid orange;
}

.about-heading{
  color:orange !important;
  text-transform: uppercase;
}

@media(max-width:468px) {
  .profile-img{
    width:10rem;
  }
  
}
/*............................*/
.Services{
  text-align: center;
  background-color:grey;
  padding-bottom: 2rem;
  justify-content: center;

}
.Services h1{
  text-transform: uppercase;
  text-align: center;
}
.Services .box{
background: whitesmoke;
border-bottom: .3125rem solid orange;
height:11rem;
width:100%;
margin-bottom: 1.875rem;
transition: 0.3s ease-in-out;
}
.Services .box:hover{
  background:orange;
  border-bottom: .3125rem solid black;
}
/*.........Experience...............*/
.experience {
  padding: 0.7rem;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: var(--primary-orange);
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: grey !important;
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}


.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;;
  border: 0.3125rem solid black;
  background: orange;
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  height: 100%;
  padding: 0 0.9375rem;
  color: grey !important;
  direction: ltr;
  line-height: 2;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: red;
  font-weight: 500;
}

/*------------ MEDIA ------------------*/
@media(max-width: 768px) {
.experience .container {
  padding-left: 0;
}

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  
  .timeline-block-left {
    float: none;
    direction: ltr; 
    width: 100%;
  }
}
/*...........Portfolio..............*/
.portfolio-wrapper {
  background: grey !important;
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: orange !important;
}

.portfolio-image {
  width: 15rem;
  border: 1px solid orange I !important;
  padding: 0 0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

/*------------ OVERFLOW BOX ------------------*/
.overflow {
  position: absolute;
   opacity: 0;
   background: black !important;
   width: 15rem;
   height: 8rem;
   top: 0;
  border: 1px solid red !important;
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: orange !important;
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}
/*------------ OVERFLOW BOX ------------------*/
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}
.popupbox-wrapper {
  margin-top: 5rem;
}

.hyper-link {
  cursor: pointer; 
  color: orange !important;
}

.hyper-link:hover {
  color: red !important;
}

/*------------ MEDIA ------------------*/
@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 570px) {
.popupbox-wrapper {
  height: 50%;
  
}
  .portfolio-image-popupbox {
    width: 50%;
    height: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }
    .portfolio-image-popupbox {
      width: 30%;
    }
  }
  /************************ TESTIMONIALS *********************************/
.testimonials {
  background-image: url(testimonials-bg.jpg);
  background-size:auto;
  background-color: black !important;
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1 {
  color: orange !important;
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonials-content {
  text-align: center;
  max-width: 60rem;
  margin: 0 auto 2rem auto;
  background: black !important; 
}

.myCarousel {
  color: white;
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 10%;
  padding-right: 10%;
  height: 17.875rem;
}

.myCarousel h3 {
  color:orange !important;
  font-weight: 600;
  text-transform: uppercase;
}

.myCarousel p {
  color: grey !important;
}

.carousel .slide img {
  width: 10rem !important;
  border-radius: 50%;
}

.carousel .slide {
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;
}
/************************ CONTACTS COMPONENT *********************************/
.contacts {
  background:black;
  padding: 3rem 0;
}

.contacts h1 {
  color: orange;
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: grey;
  font-size: 1.2rem;
}

/*------------------ INPUTS -------------------------------*/
.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid grey;
  border-radius: 0;
  color: orange;
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: orange;
  border-bottom: 0.125rem solid grey;
}

.contacts textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background:orange;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: orange;
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: white;
  background: red;
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: green !important;
}

/*---------------- MEDIA -----------------------*/
@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  
  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/************************ FOOTER COMPONENT *********************************/
.footer {
  background: black !important;
  color: grey !important;
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: grey !important;
}

.footer a:hover {
  text-decoration: none;
  color: orange !important;
  cursor:pointer;
}

.footer p {
  margin-bottom: 0;
}



